.header-color {
  background: #12c2e9; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.landing-grid {
  background: #12c2e9; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #f64f59,
    #c471ed,
    #12c2e9
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.avatar-img {
  padding-top: 5em;
  border-radius: 50% 50%;
}
.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  text-align: center;
}
.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}
.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}
.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}
.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}
.projects-grid {
  display: flex;
}
.projects-grid a {
  text-decoration: none;
}

.contact-body {
  margin: auto;

  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 3em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 70%;
  background: white;
  color: black;
  margin-bottom: 20px;
}
.contact-grid h2 {
  font-family: 'Metal Mania', cursive;
  color: #f64f59;
}
.contact-grid p {
  font-family: 'Satisfy', cursive;
  font-size: 25px;
  font-weight: bold;
  color: black;
}
.contact-grid hr {
  border-top: 2px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 35px;
  padding-right: 5rem;
  color: #c471ed;
}

.resume-right-col {
  background: #333333;
  margin-top: 2em;
  color: white;
}

@media (min-width: 320px) and (max-width: 767px) {
  .projects-grid {
    display: block;
  }
  .contact-list i {
    font-size: 25px;
    padding-right: 2rem;
    color: #c471ed;
  }
  .landing-grid {
    text-align: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .avatar-img {
    padding-top: 5em;
    border-radius: 50% 50%;
  }
  .banner-text h1 {
    font-size: 40px;
    font-weight: bold;
    color: white;
  }
  .banner-text hr {
    border-top: 2px dotted white;
    width: 50%;
    margin: auto;
  }
  .banner-text p {
    color: white;
    font-size: 15px;
    padding: 1em;
  }
  .social-links i {
    color: white;
    font-size: 1.5em;
    padding: 8px;
  }
}
